export default {
	data() {
		return {
			uid:'',
			showBlank:true,
			detail: {
				
			},
			review:[
			],
			currUser:'',
			text:'',
			userInfo:'',
		}
	},
	onLoad(e) {
		this.uid = e.uid;
		this.detailReq(e.uid);

	},
	onShow() {
		
	},
	methods: {
		async changeNice(uid, openid) {
			var data = {
				'uid':uid,
				'flag':1,
			};
			// var res = await this.$request.get('http://127.0.0.1:5000/share?uid=' + uid + 'flag=1');
			// console.log(res);
			this.detailReq(this.uid);
		},
		//动态分享
		onShareAppMessage() {
			return {
				title: '测试测试',
				path: '/pages/detail/detail?scene=',
			}
		},
		
		blank(){
			if(this.review.length > 0){
				this.showBlank = false;
			}
		},
		
		//请求数据
		async detailReq(uid){
			 var data = {
				 'uid':uid
			 };
			 var res = await this.$request.get('/share?uid=' + uid);
			 this.detail = res.data;
			 console.log(res);
			 this.review = res.data.review.reverse();
			 this.blank();
			 // console.log(res);
		},
		
		//发表评论
		reviewText(e){
			this.text = e.detail.value
		},
		
		//提交评论
		goReview(uid){
			uni.getStorage({
				key:'userInfo',
				success:(res)=>{
					this.userInfo = res.data;
					 // console.log(res.data);
					 this.reviewData(uid);
				},
				fail: (err) => {
					uni.getUserInfo({
						withCredentials:true,
						success: (res_user) => {
							uni.setStorage({
								key:"userInfo",
								data:res_user.userInfo
							})
							this.userInfo = res_user.userInfo;
							this.review(uid);
							// console.log(res_user.userInfo);
						},	
					})
				}
			})
		},	
		async reviewData(uid){
			// console.log(userInfo);
			var data = {
				'uid':uid,
				'flag':2,
				'avatarUrl':this.userInfo.avatarUrl,
				'nickName':this.userInfo.nickName,
				'content':this.text,
				'publishTime':this.$moment(new Date().getTime()).format('YYYY-MM-DD HH:mm:ss')
			};
			var res = await this.$request.get('/share',data);
			 // console.log(res);
			this.detailReq(uid);
			
		},
	},
}
